import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import CardActions from "@mui/material/CardActions";

const dispatchedEvents = [];

const SrsMuiCardActions = function (props, ref) {
  return (
    <CardActions {...props} theme={props.muiTheme}>
      <slot />
    </CardActions>
  );
};

const styles = `
  /* Custom styles for the MuiCardActions component */
`;

const props = [
  {
    name: "disableSpacing",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to CardActions if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCardActions),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
